.container--slider {
  display: flex;
  justify-content: space-between;
  box-sizing: border-box;
  position: fixed;
  z-index: 101;
  width: 77px;
  height: 240px;

  right: 0;
  top: 22rem;
  margin-right: 80px;
  filter: drop-shadow(0px 0px 100px rgba(0, 0, 0, 0.2));
}

.container--index {
  display: flex;
  justify-content: center;
  box-sizing: border-box;
  font-family: Gilroylight;
  font-weight: bold;
  font-size: 1.125rem;
  flex-direction: column;
}

.list--itens {
  display: flex;
  justify-content: flex-end;
  text-decoration: none;
  color: #ffff;
}

.list--margin {
  margin-bottom: 2.5rem;
}

.container--scroll {
  position: relative;
  height: inherit;
  width: 3px;
  background-color: rgba(255, 255, 255, 0.5);
}

.scroll--indicator {
  position: absolute;
  background-color: #ffff;
  height: 60px;
  width: 3px;
  transition-duration: 1000ms;
}

@media (max-width: 768px) {
  .container--slider {
    display: flex;
    justify-content: space-between;
    box-sizing: border-box;
    position: fixed;
    z-index: 101;
    width: 77px;
    height: 240px;
  
    right: 0;
    top: 22rem;
    margin-right: 30px;
    filter: drop-shadow(0px 0px 100px rgba(0, 0, 0, 0.2));
  }
}

@media (max-width: 425px) {
  .container--slider {
    display: flex;
    box-sizing: border-box;
    position: fixed;
    z-index: 101;
    width: 47px;
    height: 240px;
  
    right: 0;
    top: 22rem;
    margin-right: 20px;
    filter: drop-shadow(0px 0px 100px rgba(0, 0, 0, 0.2));
  }

  .container--index {
    display: flex;
    justify-content: center;
    box-sizing: border-box;
    font-family: Gilroylight;
    font-weight: bold;
    font-size: 0.9rem;
    flex-direction: column;
  }
}

@media (max-width: 375px) {
  .container--slider {
    display: flex;
    box-sizing: border-box;
    position: fixed;
    z-index: 101;
    width: 37px;
    height: 240px;
  
    right: 0;
    top: 34rem;
    margin-right: 10px;
    filter: drop-shadow(0px 0px 100px rgba(0, 0, 0, 0.2));
  }

  .container--index {
    display: flex;
    justify-content: center;
    box-sizing: border-box;
    font-family: Gilroylight;
    font-weight: bold;
    font-size: 0.7rem;
    flex-direction: column;
  }
}


